import React, { Component } from 'react';
import styled from "styled-components";
import "../css/style.css";
import "../css/table.css";
import { t } from "i18next";
const StyledMenu = styled.button`

`
styled.li`
 color: #000;

`;


class ResultTable extends Component {
    render() {
        const{bettingresult, userbettingresult} = this.props;
        
        var index = 0;
        var bettinglist =[];
          
        bettingresult.forEach((obj)=>{
            ++index;
            var bettinginfo =
            <tr class= {index%2==0 ? "bet_result_table_white" : ""} >
                <td>{obj.no_betting}{t('number_horse')}</td>
                <td>{obj.user_name}</td>
                <td>{obj.reward_rate}</td>
                <td>{obj.reward_value}</td>
            </tr>   
            bettinglist.push(bettinginfo);      
        });

        index = 0;
        var userbettinglist =[]; 
        userbettingresult.forEach((obj)=>{
            ++index;
            var bettinginfo =
            <tr class= {index%2==0 ? "bet_result_table_white" : ""} >
                {/* <td>{index} 회차</td>
                <td>{moment(obj.time).format('hh:mm A')}</td> */}
                <td>{obj.no_betting}{t('number_horse')}</td>
                <td>{obj.betting_value}</td>
                <td>{obj.reward_rate}</td>
                <td>{obj.reward_value}</td>
            </tr>   
            userbettinglist.push(bettinginfo);      
        });
        // return (<div>TEST</div>)
        return (
            <div className="table">
                <div class="bet_result_title">{t('total_victory_list')}</div>

                    <table class="timetable table table-striped">
                        <thead>
                        <tr>
                            <th>{t('victory_number')}</th><th>{t('id')}</th><th>{t('bet_rate')}</th><th>{t('price')}</th>
                        </tr>
                        </thead>
                        <tbody>
                            {bettinglist}

                        </tbody>
                    </table>

                    <div class="bet_result_title my_result">{t('my_result')}</div>

                    <table class="timetable table table-striped">
                    <thead>
                        <tr>
                            <th>{t('victory_number')}</th><th>{t('betting_price')}</th><th>{t('bet_rate')}</th><th>{t('price')}</th>
                        </tr>
                        </thead>
                        <tbody>
                            {/* <tr class="bet_result_table_white">
                                <td>7번마</td><td>2,000</td><td>4,000</td>
                            </tr> */}
                            {userbettinglist}
                        </tbody>
                    </table>
            </div>
        );
    }
}

export default ResultTable;