function BettingBoard(props) {
  var dispatch = useDispatch();
  var remaintime = useSelector((state) => state.remaintime);

  var betexample = props.props.betexample;
  var betinfo = props.props.savebetinfo;
  var betprice = props.props.betprice;
  var lines = [];
  var index = 0;
  var totalprice = 0;

  var priceselects = [];

  for (var i = 0; i < betexample.length; ++i) {
    priceselects.push(
      <option value={betexample[i]}>{betexample[i]} METAF GAS</option>
    );
  }

  for (var i = 0; i < horseCount; ++i) {
    ++index;
    var betting_value = betprice[i];
    var betting_rate = betinfo[`betting_rate_${index}`];
    var totalprice = Math.round(betting_value * betting_rate);

    var line = (
      <tr key={index}>
        {" "}
        <td>
          {index} {t("number_horse")}
        </td>
        <td>
          <form className="table-form">
            <select
              id={index}
              key={index}
              className={classnames(["table-select", "priceselect_" + index])}
              value={betprice[i]}
              onChange={function (e) {
                props.props.callbetting(
                  e.target.id,
                  betexample[e.target.selectedIndex - 1]
                );
              }}
              name="bet"
            >
              <option value="0" hidden>
                {t("select_price")}
              </option>
              {priceselects}
            </select>
          </form>
        </td>
        <td>{betting_rate}</td> <td>{totalprice}</td>
      </tr>
    );

    lines.push(line);
  }

  var islock =
    betinfo.progress == config.BETTING_PROGRESS_BETTING_CLOSING &&
    remaintime <= 0;

  for (var i = 0; i < horseCount; ++i) {
    var index = i + 1;
    var selectname = ".priceselect_" + index;

    if (betprice[i] > 0 || islock) $(selectname).prop("disabled", true);
  }
  //var startTime = betinfo.time != undefined ? moment(betinfo.time).format('YYYY-MM-DD HH:MM A') : '0';

  var startTime =
    betinfo.progress == config.BETTING_PROGRESS_BETTING_CLOSING
      ? `${t("betting_remaintime")} ${remaintime} ${t("second")}`
      : "";

  //var blockpanel = betinfo.progress == config.BETTING_PROGRESS_BETTING_CLOSING && remaintime <= 0 ? "BLOCK BOARD" : "";
  //var startTime = `${t('betting_remaintime')} ${remaintime} ${t('second')}`;
  return (
    <div>
      {/* {blockpanel} */}
      <span className="title bet_result_title">{t("betting_board")}</span>
      <div className="time">
        {startTime}
        <span classsName="round">
          {betinfo.count_racing} {t("round")}
        </span>
      </div>
      <div>
        <table className="table table-striped">
          <tbody>
            <tr>
              <th>{t("line")}</th>
              <th>{t("betting_price")}</th>
              <th>{t("bet_rate")}</th>
              <th>{t("victory_pricce")}</th>
            </tr>
            {lines}
          </tbody>
          <tfoot>
            <tr>
              <td colSpan="2">{t("total_betting_price")}</td>
              <td colSpan="3">
                {betinfo.total_betting_value} {t("metaf_gas")}
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
      <h4>{t("max_betting_price_notice")}</h4>
    </div>
  );
}

function GamePlay(props) {
  if (config.GAME_MOVIE_PLAY) {
    return (
      <div className="App">
        <ReactPlayer
          className="player"
          url={props.gameplayurl}
          //src=
          width="100%"
          heigth="100%"
          playing={true}
          muted={true}
          controls={true}
        />
      </div>
    );
  } else {
    const eventIndex =
      props.progress == config.BETTING_PROGRESS_GAME_PLAYING_UNITY
        ? config.GAMEPLAY_EVNETINDEX
        : 0;
    const height = 650;
    var gameurl = config.REACT_APP_RACING_UNITY_PORT == '' ? `${config.REACT_APP_RACING_UNITY_URL}/?racingCount=0&eventIndex=${eventIndex}` : 
    `${config.REACT_APP_RACING_UNITY_URL}:${config.REACT_APP_RACING_UNITY_PORT}/?racingCount=0&eventIndex=${eventIndex}`;
    return (
      <div>
        <p style={{ align: "center" }}>
          {" "}
          <iframe
            className="iframeTimeTable"
            frameBorder={0}
            allowFullScreen
            //key={this.state.random}
            style={{
              width: "100%",
              maxHeight: height,
              height: height,
              border: 0,
              // padding : -300,
              // margin : -650,
              padding: 0,
              margin: 0,
              left: 30,
              top: 30,
            }}
            //src='/racing-schedule-popup'
            //src='http://localhost:3205/racing-schedule-popup'
            //src='http://192.168.0.170:64052/?racingCount=2'
            src={gameurl}
            //src={`${config.REACT_APP_RACING_UNITY_URL}:${config.REACT_APP_RACING_UNITY_PORT}/?racingCount=0&eventIndex=${eventIndex}`}
          />
        </p>
      </div>
    );
  }
}

function GameClosing(props) {
  return (
    <div>
      <RacingResultPopup
        bettingresult={props.bettingresult}
        userbettingresult={props.userbettingresult}
      ></RacingResultPopup>
    </div>
  );
}

function Page1(props) {
  if (!props.bettingPlaying && props.timetable.length > 0) {
    return <RacingTimeTablePopup list={props.timetable}></RacingTimeTablePopup>;
  }

  if(isBettingBoard)
    return (<div><BettingBoard props={props} /></div>);
  else
  return (
    <div>
      <GamePlay
        gameplayurl={props.gameplayurl}
        progress={props.savebetinfo.progress}
      />
    </div>
  );

  switch (props.savebetinfo.progress) {
    case config.BETTING_PROGRESS_HORSE_INTRODUCE_UNITY:
      return (
        <div>
          <GamePlay
            gameplayurl={props.gameplayurl}
            progress={props.savebetinfo.progress}
          />
        </div>
      );
    case config.BETTING_PROGRESS_BETTING:
    case config.BETTING_PROGRESS_BETTING_CLOSING:
      return (
        <div>
          <BettingBoard props={props} />
        </div>
      );
    case config.BETTING_PROGRESS_GAME_PLAYING_UNITY:
      var battleresult = props.showgameresult ? (
        <GameClosing
          bettingresult={props.bettingresult}
          userbettingresult={props.userbettingresult}
        />
      ) : (
        ""
      );
      var gameplay = !props.showgameresult ? (
        <GamePlay
          gameplayurl={props.gameplayurl}
          progress={props.savebetinfo.progress}
        />
      ) : (
        ""
      );
      return (
        <div>
          {gameplay}
          {battleresult}
        </div>
      );
    case config.BETTING_PROGRESS_GAME_CLOSING:
      // return(<GamePlay gameplayurl={props.gameplayurl}/>);
      // var gamebetting = props.savebetinfo.progress == config.BETTING_PROGRESS_OPEN ? <BettingBoard props={props}/> : "";
      // var gameclosing = props.savebetinfo.progress == config.BETTING_PROGRESS_GAME_CLOSING ? <GameClosing
      // style={{
      //   marginTop: '-500px'
      // }}
      // bettingresult={props.bettingresult} userbettingresult={props.userbettingresult}/>
      // : "";
      return (
        <div>
          {/* <GamePlay gameplayurl={props.gameplayurl} progress={props.savebetinfo.progress}/>
        {gamebetting}
        {gameclosing} */}
          {
            <GameClosing
              bettingresult={props.bettingresult}
              userbettingresult={props.userbettingresult}
            />
          }
        </div>
      );
  }

  return <div></div>;
}

// 쿠키 생성 함수
function setCookie(cName, cValue, cSecond) {
  var expire = new Date();
  var second = cSecond * 1000;
  expire.setTime(expire.getTime() + second);
  var cookies = cName + "=" + cValue + "; path=/ "; // 한글 깨짐을 막기위해 escape(cValue)를 합니다.
  if (typeof cDay != "undefined")
    cookies += ";expires=" + expire.toGMTString() + ";";
  document.cookie = cookies;
}

// 쿠키 가져오기 함수
function getCookie(cName) {
  cName = cName + "=";
  var cookieData = document.cookie;
  var start = cookieData.indexOf(cName);
  var cValue = "";
  if (start != -1) {
    start += cName.length;
    var end = cookieData.indexOf(";", start);
    if (end == -1) end = cookieData.length;
    cValue = cookieData.substring(start, end);
  }
  return cValue;
}

export default RacingSchedulePopup;
