import React, { Component } from "react";
import styled from "styled-components";
import "../css/style.css";
import "../css/table.css";
import moment from "moment-timezone";
import { t } from "i18next";

const StyledMenu = styled.button``;
styled.li`
  color: #000;
`;

class TimeTable extends Component {
  render() {
    var index = 0;
    const { list } = this.props;
    var pushlist = [];
    list.forEach((obj) => {
      console.log("타임", obj.time);
      ++index;
      var timeinfo = (
        <tr class={index % 2 == 0 ? "bet_result_table_white" : ""}>
          <td>
            {index} {t("racing_count2")}
          </td>
          {/* <td>{moment(obj.time).format("hh:mm A")}</td> */}
          <td>
            {moment(obj.time).tz("Asia/Seoul").format("YYYY-MM-DD HH:mm:ss")}
          </td>
        </tr>
      );
      pushlist.push(timeinfo);
    });

    return (
      <div className="table">
        <div class="bet_result_title">{t("racing_schedule")}</div>
        <table class="timetable table table-striped">
          <thead>
            <tr>
              <th>{t("racing_count")}</th>
              <th>{t("time")}</th>
            </tr>
          </thead>
          <tbody>{pushlist.slice(0, 4)}</tbody>
        </table>
      </div>
    );
  }
}

export default TimeTable;
